* {
  margin: 0;
  padding: 0;
}

#wrapper {
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#contenedorHeader {
  align-items: center;
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
  box-shadow: 0px 0px 10px 2px gray;
  display: flex;
  height: 75px;
  justify-content: space-between;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}
#contenedorHeader span {
  color: rgb(0, 153, 255);
  font-size: 2rem;
}
#contenedorLogo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
}
#seccionInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 150px;
  background-color: rgb(245, 245, 245);
  width: 100%;
  padding: 50px 0px 50px;
  height: 70vh;
}
@media only screen and (max-width: 624px) {
  #seccionInfo {
    margin-top: 75px;
    height: 100%;
    width: 100%;
  }
}
#articuloInformacion {
  display: flex;
  width: 90%;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 55px;
  padding: 40px 0px 20px 0px;
}
#contenedorFotoYtexto {
  display: flex;
  width: 90%;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 40px;
  text-align: center;
}

#contenedorFotoYtexto div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 500px;
  gap: 30px;
}
#contenedorFotoYtexto div h2 {
  font-weight: 100;
  font-size: 3.5rem;
  color: rgb(0, 153, 255);
}
#contenedorFotoYtexto div h2 {
  font-weight: 100;
  font-size: 3rem;
  color: rgb(0, 153, 255);
}
#BotonCV {
  background-color: rgb(0, 153, 255);
  border: none;
  width: 350px;
  border-radius: 20px;
}

#BotonCV a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-size: 1.3rem;
  padding: 15px;
  border: none;
}

#BotonCV :hover {
  background-color: rgb(132, 206, 255);
  border-radius: 20px;
  box-shadow: 0px 0px 5px 0px gray;
  color: black;
}
#botonesContactar {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;

  justify-content: space-evenly;
}

#botonesContactar a {
  opacity: 40%;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#botonesContactar a:hover {
  animation: botonContacto 0.8s;
  animation-fill-mode: both;
}
#botonesContactar a:hover::before {
  animation: botonContacto 0.8s;
  animation-fill-mode: both;
  animation-direction: reverse;
}
#botonesContactar a img {
  width: 50px;
}

@keyframes botonContacto {
  100% {
    opacity: 100%;
    box-shadow: 0px 0px 4px rgb(89, 89, 89);
    border-radius: 100%;
  }
}

@keyframes colorLogo {
  0% {
    box-shadow: 0px 0px 20px rgb(0, 153, 255);
  }
  10% {
    box-shadow: 0px 0px 40px rgb(0, 153, 255);
  }
  20% {
    box-shadow: 0px 0px 60px rgb(0, 153, 255);
  }
  30% {
    box-shadow: 0px 0px 80px 10px rgb(0, 153, 255);
  }
  40% {
    box-shadow: 0px 0px 100px 0px rgb(0, 153, 255);
  }
  50% {
    box-shadow: 0px 0px 110px 0px rgb(0, 153, 255);
  }
  60% {
    box-shadow: 0px 0px 100px 0px rgb(0, 153, 255);
  }
  70% {
    box-shadow: 0px 0px 80px 0px rgb(0, 153, 255);
  }
  80% {
    box-shadow: 0px 0px 60px 0px rgb(0, 153, 255);
  }
  90% {
    box-shadow: 0px 0px 40px 0px rgb(0, 153, 255);
  }
  100% {
    box-shadow: 0px 0px 20px 0px rgb(0, 153, 255);
  }
}
#fotoPersonal {
  width: 40%;
  max-width: 250px;
  min-width: 200px;
  border-radius: 50%;
  animation: colorLogo 5s infinite;
}

#seccionHabilidades {
  margin-top: 75px;
  background-color: rgb(245, 245, 245);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}
#seccionHabilidades h2 {
  margin: 30px;
  font-weight: 100;
  font-size: 2rem;
  border-bottom: 1px solid lightgray;
  width: 100%;
  text-align: center;
  padding-bottom: 10px;
}
#seccionHabilidades article {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 45px;
  justify-content: center;
  padding: 20px;
}

#lenguajeProgramacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 20px;
  height: 120px;
  box-shadow: 0px 0px 5px 0px rgb(109, 109, 109);
  border-radius: 10px;
  background-color: white;
}
#lenguajeProgramacion p {
  display: none;
}
#lenguajeProgramacion:hover p {
  display: flex;
  animation: plenguajes 1s;
  animation-iteration-count: 1;
}

@keyframes plenguajes {
  from {
    transform: translate(0, -15px);
    opacity: 0%;
    display: block;
  }
  to {
    transform: translate(0, 0px);
    opacity: 100%;
    display: block;
  }
}
#lenguajeProgramacion img {
  width: 80%;
}
#lenguajeProgramacion:hover img {
  animation: lenguajeAnimado 1s;
  animation-fill-mode: both;
}
#lenguajeProgramacion:hover {
  box-shadow: 0px 0px 5px 0px rgb(0, 153, 255);
  color: black;
}

@keyframes lenguajeAnimado {
  from {
    transform: translate(0, +10px);
  }
  to {
    transform: translate(0, -15px);
  }
}
#seccionProyectos {
  margin-top: 75px;
  background-color: rgb(245, 245, 245);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}
#seccionProyectos h2 {
  margin: 30px;
  font-weight: 100;
  font-size: 2rem;
  border-bottom: 1px solid lightgray;
  width: 100%;
  text-align: center;
  padding-bottom: 10px;
}
#contenedorProyectos {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
}

#fetch {
  background-image: url('../img/proyectos/fetch.jpg');
}
#preguntas {
  background-image: url('../img/proyectos/preguntas.jpg');
}
#rampUp {
  background-image: url('../img/proyectos/rampUp.jpg');
}
#cronometro {
  background-image: url('../img/proyectos/cronometro.jpg');
}

.proyecto {
  background-repeat: no-repeat;
  background-size: cover;
  background-clip: initial;
  width: 250px;
  height: 400px;
  box-shadow: 0px 0px 10px 2px gray;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 624px) {
  .proyecto {
    width: 90%;
  }
}
.divTecnologiasProyectos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  opacity: 0%;
}

.proyecto div a {
  background-color: rgba(249, 249, 249, 0.9);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.proyecto div a img {
  width: 50px;
}
.divTecnologiasProyectos:hover {
  animation: mostrar 0.5s;
  animation-fill-mode: both;
}

@keyframes mostrar {
  from {
    background-color: none;
    opacity: 0%;
  }
  to {
    opacity: 100%;
    background-color: rgba(0, 0, 0, 0.479);
    box-shadow: 0px 0px 10px 0px black;
  }
}

#divGrafica {
  width: 100%;
  margin-top: 75px;
  background-color: rgb(245, 245, 245);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding-bottom: 50px;
}

#contenedorGrafica {
  width: 80%;
  margin-top: 20px;
  background-color: white;
  padding: 20px;
  box-shadow: 0px 0px 10px 5px lightgray;
}

#seccionGrafica {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 75px;
  background-color: rgb(245, 245, 245);
  width: 100%;
}
#botonesGrafica {
  display: flex;
  width: 100%;
  margin-top: 20px;
  padding: 10px 0px 10px;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}
#botonesGrafica h2 {
  padding: 30px 0px 10px;
  text-align: center;
  font-weight: 100;
  font-size: 2.5rem;
  width: 90%;
  border-bottom: 1px solid lightgray;
}
.botonGrafica {
  padding: 10px;
  min-width: 400px;
  border: none;
  background-color: rgb(215, 215, 215);
  border: 1px solid rgb(172, 172, 172);
  color: black;
  border-radius: 5px;
  font-size: 1.2rem;
}
.botonGrafica:hover {
  box-shadow: 0px 0px 5px gray;
  background-color: rgb(243, 243, 243);
  cursor: pointer;
}
